<template>
  <div class="classic-browse-page">
    <div class="page-bg">
      <div class="page-bg-gradient" />
    </div>
    <div class="browse-inner">
      <section class="header">
        <h1 class="title title-font">分类浏览</h1>
        <p class="subtitle content-font">按分类、作者、朝代、体裁筛选</p>
      </section>
      <div class="browse-layout">
        <aside class="filter-sidebar">
          <div class="filter-card">
            <el-form :inline="true" class="filter-form">
              <el-form-item label="分类">
                <el-input
                  v-model="filters.categories"
                  placeholder="如：家国情怀"
                  clearable
                  style="width: 140px"
                />
              </el-form-item>
              <el-form-item label="作者">
                <el-input
                  v-model="filters.authors"
                  placeholder="如：杜甫"
                  clearable
                  style="width: 120px"
                />
              </el-form-item>
              <el-form-item label="朝代">
                <el-select
                  v-model="filters.dynasties"
                  placeholder="朝代"
                  clearable
                  style="width: 120px"
                >
                  <el-option v-for="d in dynastyOpts" :key="d" :label="d" :value="d" />
                </el-select>
              </el-form-item>
              <el-form-item label="体裁">
                <el-input
                  v-model="filters.genres"
                  placeholder="如：诗"
                  clearable
                  style="width: 100px"
                />
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="applyFilters">筛选</el-button>
                <el-button @click="randomBatch">随机一批</el-button>
              </el-form-item>
            </el-form>
          </div>
        </aside>
        <div ref="contentAreaRef" class="content-area">
          <div v-if="totalPages > 1" class="pagination-wrap pagination-top">
            <el-pagination
              v-model:current-page="currentPage"
              :page-size="pageSize"
              :total="items.length"
              layout="prev, pager, next"
            />
          </div>
          <div v-if="loading" class="loading-wrap">
            <el-icon class="is-loading" :size="32"><Loading /></el-icon>
          </div>
          <div v-else class="list-wrap">
            <router-link
              v-for="item in currentPageItems"
              :key="item.id"
              :to="`/classic/item/${item.id}`"
              class="item-card"
            >
              <p class="original content-font">{{ item.original_text }}</p>
              <p v-if="item.translation" class="translation">{{ item.translation }}</p>
              <div class="meta">
                <span v-if="item.author">{{ item.author }}</span>
                <span v-if="item.dynasty">{{ item.dynasty }}</span>
                <span v-if="item.genre">{{ item.genre }}</span>
              </div>
            </router-link>
          </div>
          <div v-if="totalPages > 1" class="pagination-wrap">
            <el-pagination
              v-model:current-page="currentPage"
              :page-size="pageSize"
              :total="items.length"
              layout="prev, pager, next"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted, watch, nextTick } from 'vue'
import { Loading } from '@element-plus/icons-vue'
import { browse } from '@/api/classic'
import type { ClassicItem } from '@/types/classic'

const dynastyOpts = [
  '先秦',
  '汉',
  '三国',
  '晋',
  '南北朝',
  '隋',
  '唐',
  '五代',
  '宋',
  '辽',
  '金',
  '元',
  '明',
  '清',
  '近现代',
]

const filters = reactive({
  categories: '',
  authors: '',
  dynasties: '',
  genres: '',
})
const items = ref<ClassicItem[]>([])
const loading = ref(false)
const pageSize = 20
const currentPage = ref(1)

const totalPages = computed(() => Math.ceil(items.value.length / pageSize) || 1)
const currentPageItems = computed(() => {
  const start = (currentPage.value - 1) * pageSize
  return items.value.slice(start, start + pageSize)
})

function toArray(s: string): string[] {
  if (!s?.trim()) return []
  return [s.trim()]
}

async function applyFilters() {
  loading.value = true
  try {
    const list = await browse({
      categories: toArray(filters.categories),
      authors: toArray(filters.authors),
      dynasties: toArray(filters.dynasties),
      genres: toArray(filters.genres),
    })
    items.value = list ?? []
    currentPage.value = 1
  } finally {
    loading.value = false
  }
}

async function randomBatch() {
  loading.value = true
  try {
    const list = await browse({ random: 20 })
    items.value = list ?? []
    currentPage.value = 1
  } finally {
    loading.value = false
  }
}

const contentAreaRef = ref<HTMLElement | null>(null)

watch(currentPage, () => {
  nextTick(() => {
    contentAreaRef.value?.scrollIntoView({ behavior: 'smooth', block: 'start' })
  })
})

onMounted(() => {
  randomBatch()
})
</script>

